.filterContainer{
    background-color: #67a8b4;
    overflow: hidden;
    transition: max-height 0.s linear;
    width: 250px;
    float: right;
    margin-top: -1%;
    padding: 0 30px;
    padding-bottom: 25px;
    transition-duration: 0.4s;
    border-radius: 10px;
}

.closeFil{
    transform: translateX(310px);
}

.filminititle{
    color: white;
    margin-bottom: 8px;
}

.costBar{
    display: flex;
    color: #67a8b467;
    background-color: white;
    border-radius: 5px;
    font-size: 15px;
    justify-content: space-around;
    height: 25px;
    padding: 0 15px;
    width: 50%;
    padding-top: 3px;
    margin-bottom: 5px;
}

.dollarhov:hover{
    color: #67a8b4d2;
    cursor: pointer;
}

.hardenCost{
    color: #67a8b4;
}

.catBox{
    width: 98%;
    background-color: white;
    border-radius: 5px;
    padding: 5px 10px;
    color: #5f9ca7;
    padding-bottom: 15px;
}

.divBar{
    width: calc(100%-15px);
    height: 1px;
    background-color: #5f9ca7e1;
    margin: 2px auto;
}

.catSplit{
    display:grid;
    grid-template-columns: 1fr 1fr;
}

.catCol{
    width: 100%;
}

.entriesCat{
    height: 25px;
}