.vidContainer_vidc{
    position: relative;
    overflow-x: hidden;
    height: calc(100vh - 150px);
}

.topcolor_vidp{
    background-color: #67a8b4;
    padding: 20px 0;
}

.whiteback{
    background-color: white;
    width: 100vw;
    position: absolute;
    background-color: #67a8b4;
    display: grid;
    overflow-y: scroll;
}

.martop_vidc{
    padding-top: 35px;
}

.vidDisp_vid{
    position: relative;
    overflow: hidden;
    background-color: white;
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fit, 24rem);
    justify-content: center;
    padding: 20px 50px;
}

.vidUse{
    position: relative;
}

.addVideo{
    background-color: #4E828B;
    width: 160px;
    padding: 5px 10px;
    border-radius: 5px;
    margin: 5px;
    color: white;
}

.addVideo:hover{
    cursor: pointer;
    background-color: rgb(62, 98, 104);
}