.ADbox{
    padding: 30px 25px;
    display: grid;
    width: 400px;
}

.ADinput{
    margin-bottom: 9px;
    width: 100%;
}

.ADsplit{
    display: grid;
    grid-template-columns: 2fr 1fr;
}

.ADsplit{
    display: flex;
}

.ADtext{
    font-size: 25px;
    font-weight: bold;
}

.ADWide{
    width: 49%;
}