@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto Regular'), 
         local('Roboto-Regular'), 
         url(https://themes.googleusercontent.com/static/fonts/roboto/v11/2UX7WLTfW3W8TclTUvlFyQ.woff) 
         format('woff');
}

.main-container {
    display: flexbox;
    flex-direction: column;
    overflow: hidden;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 0.4fr;
    gap: 0px 0px;
    grid-template-areas:
      "left right";
}

.left 
{ 
    display: grid;
    grid-area: left;
	grid-template-rows: 0.1fr 0.4fr;
	grid-gap: 1em;
	justify-content: center;
    background-color: #B6E0CA;
}

.right 
{ 
    display: grid;
    grid-area: right; 
    grid-template-rows: 0.2fr 0.1fr 0.1fr;
	grid-gap: 0.5em;
	justify-content: space-around;
    background-color: #B6E0CA;
}

.footer 
{ 
    display: grid;
    flex-direction: column;
    background-color: #67A8B4;
}

.header{
	display: grid;
	grid-template-rows: 0.3fr 0.2fr;
	align-items: center;
}

.header > h2 {
	color:#ffffff;
	font-size: 45px;
	text-align: center;
	font-weight: 500;
    margin-bottom: 0px;
}

.header > hr {
    margin: 0px 0px;
}

.header > h4 {
	color:#619398;
	font-size: 40px;
	font-weight: 400;
	text-align:center;
    margin: 0px 0px;
    padding: 0px 0px;
}

