.DDbox{
    padding: 20px 20px;
}

.DDbuttonbox{
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}

.DDbutton{
    border-radius: 5px;
    padding: 8px 40px;
    border-width: 0px;
}

.DDbutton:hover{
    cursor: pointer;
}

.DDleft{
    background-color: rgba(255, 0, 0, 0.212);
}

.DDleft:hover{
    background-color: rgba(255, 0, 0, 0.325);
}

.DDright{
    background-color: rgba(0, 0, 255, 0.212);
}

.DDright:hover{
    background-color: rgba(0, 0, 255, 0.325);
}