.searchCard{
    width: 90%;
    background-color: white;
    margin-bottom: 25px;
    padding: 4% 5%;
    display:grid;
    position: relative;
}

.carTitle{
    font-size: 21px;
    color: #4e828b;
    font-weight: 500;
}

.carddiv{
    background-color: #4e828b;
    height: 1px;
    margin: 8px 0px;
}

.cardflex{
    display: flex;
}

.cardTitle{
    font-weight: 500;
}

.topright{
    position: absolute;
    right: 0;
    display: flex;
}

.butright{
    margin: 10px 0px;
    margin-right: 10px;
    padding: 5px 10px;
    border-radius: 3px;
    background-color: rgb(68, 174, 201);
}

.butright:hover{
    background-color: rgb(59, 153, 177);
    cursor: pointer;
}
