.searchContainer{
    background-color: #67a8b4;
    overflow-y: hidden;
    height: calc(100vh - 150px);
}

.button:hover{
    cursor: pointer;
}

.barBox{
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
    height: 13%;
}

.seudoBar{
    margin-left: 50px;
    padding-top: 5px;
    height: 40px;
    border-width: 0px;
    width: 30%;
    border-radius: 30px;
    padding-left: 20px;
    background-color: white;
    display: flex;
    align-items: center;
    padding-right: 7px;
}

.searchBar{
    width: 85%;
    margin-right: 2%;
    border-width: 0px;
    padding-left: 20px;
    font-size: 20px;
}

.searchBar:focus{
    outline: none;
}

.searchIcon{
    color: #67a8b4;
    font-size: 22px;
    padding-top: 5px;
}

.searchIcon:hover{
    color: #558891;
}

.filterButton{
    display: flex;
    align-items: center;
    color: white;
    font-size: 22px;
    transition-duration: 0.3s;
}

.menuIcon{
    padding-top: 10px;
    padding-right: 5px;
}

.filterButton:hover{
    color: rgb(236, 230, 230);
}

.moveFilter{
    transform: translateX(-180px);
}

.reflect{
    transform: scaleX(-1);
}

.fil{
    z-index: 2;
    position: relative;
}

.bottom{
    background-color: #67a8b4;
    display: grid;
    grid-template-columns: 2.5fr 1.5fr;
    width: 100%;
    z-index: 1;
    position: absolute;
    overflow-y: hidden;
    height: 87%;
}

.rightSide{
    padding: 0 10%;
    overflow: scroll;
    height: 70%;
}

.rightSide::-webkit-scrollbar {
    display: none;
}
  
.rightSide {
-ms-overflow-style: none;  
scrollbar-width: none;  
}

.leftSide{
    padding-left: 6%;
    height: 70%;
    width: 94%;
}

.subText{
    color: white;
}

.searchintro{
    position: absolute;
    margin: auto;
    padding-top: 30px;
    width: 100%;
    overflow-y: hidden;
    text-align: center;
    color: white;
    font-size: 28px;
}

.support{
    width: 30%;
}

.rightSide{
    height: 90%;
}

.leftSide{
    height: 90%;
}

.midLoad{
    width: 100vw;
    padding: 100px auto;
    text-align: center;
    color: white;
    font-size: 40px;
    font-weight: 500;
}

.addB{
    padding-top: 22px;
    margin-left: 15px;
    color: blue;
    font-weight: bold; 
}

.addB:hover{
    color: black;
    cursor: pointer;
}

