  
.showText{
    color: black;
}

.markerText{
    text-align: center;
    font-size: 15px;
    font-weight: bold;
    text-shadow: 4px 4px 100px #ececec;
    width: calc(18px + 50px);
    margin-left: -25px;
    height: 18px;
    color: transparent
}

.pin{
    font-size: 18px;
}

.markContainer{
    transition-duration: 0.3s;
}

.hovering{
    transform: scale(2);
    color: red;
    margin-left: -5px;
    margin-top: -5px;
    padding-bottom: 5px;
}