.vidcardcont_vid{
    overflow: hidden;
    border-radius: 1rem;
    position: relative;
    margin: 1rem;
    padding: 15px 0;
    text-align: center;
}

.vidcardcont_vid:hover{
    background-color: #4e828b23;
}

.t_v:hover{
    cursor: pointer;
}

.padtop_vidTmb:hover{
    cursor: pointer;
}

.t_v{
    padding:  0px 8%;
}

.vidName_vid{
    color: #4e828b;
    font-weight: 400;
    font-size: 23px;
    font-weight: 500;
}

.subtext_vid{
    padding-top: 5%;
    font-size: 18px;
}

.bar_vid{
    height: 1px;
    width: 100%;
    margin: auto;
    background-color: black;
    margin: 10px 0;
}

.vidTmbContain{
    width: 100%;
}

.padtop_vidTmb{
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    position: relative;
    bottom: 0;
}

.vidbutton_vid{
    position: relative;
    display: flex;
    width: 100%;
    text-align: center;
    padding:  0 210px;
}