.NavContainer{
    height: 150px;
    background-color:#67A8B4;
    display: grid;
    grid-template-rows: 3fr 2fr;
}

.logo{
    background: transparent;
    opacity: 1;
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
    margin-top: 0.5em;
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 1fr 0.5fr;
}

.logoimg{
    background: transparent;
    opacity: 1;
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
}
.vertical_line{
    border-left: thick solid #ffffff;
}

.options{
    display:grid;
    grid-template-columns: 1.9fr 0.1fr 1.5fr 0.1fr 1.5fr 0.1fr 1.5fr 0.1fr 1.7fr 0.1fr 1.5fr;
    color: #ffffff;
    font-family: Roboto;
    font-size: 25px;
    justify-content: center;
    margin-left: 2.5em;
    margin-right: 2.5em;
    grid-auto-rows: 60px;
}

@media (max-width: 1300px) {
  .options {
    font-size: 20px;
  }
  .biglink{
      padding-top: 5px;
      padding-bottom: 7px;
  }
}

@media (max-width: 1100px) {
    .options {
      font-size: 17px;
    }
    .biglink{
        padding-top: 8px;
        padding-bottom: 16px;
        line-height: 1rem;
    }
}

@media (max-width: 900px) {
    .options{
        margin: 0 20px;
    }
}

.options > div{
    text-align: center;
    margin-top: 0.5em;
}

.lower-bar{
    background-color: #4E828B;
}

.bar{
    height: 5px;
    border: 0;
    background: #ffffff;
    justify-self: bottom;
    align-self: bottom;
    opacity: 1;
    margin-bottom: 0;
}

a{
    color:inherit;
    text-decoration: none;
}

.biglink{
    display:block;
    width: 100%;
}

.loginbutton{
    float: right;
}