
/*New code from here on out*/
.main_M{
	background-color:#b6dfc9;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 1em;
	justify-content: center;
}

.main_M > div{
	padding: 1em;
} 

.left_M{
	display: grid;
	grid-template-rows: 1fr 7fr;
	grid-gap: 1em;
	justify-content: center;
}
.right_M{
	display: grid;
	grid-template-rows: 0.6fr 0.6fr 3fr;
	grid-gap: 2em;
	justify-content: center;
	background-color:#67A8B4;
}

.heading_M{
	display: grid;
	grid-template-rows: 1fr 1fr;
	align-items: center;
}
.heading_top{
	color:#ffffff;
	font-size: 45px;
	text-align:center;
	font-weight: 400;
}
.heading_bottom{
	color:#619398;
	font-size: 30px;
	font-weight: 400;
	text-align:center;
	top: -15;
}
